.siderbar-cuenta {
  width: 400px;
  height: calc(100vh - 50px);
  z-index: 998;
  position: fixed;
  right: 0;
  top: 50px;
  -webkit-box-shadow: -2px 5px 10px 1px rgba(0,0,0,.176);
          box-shadow: -2px 5px 10px 1px rgba(0,0,0,.176);
  -webkit-transform: translateX(450px);
      -ms-transform: translateX(450px);
          transform: translateX(450px);
  -webkit-transition: -webkit-transform .2s ease-in-out;
  transition: -webkit-transform .2s ease-in-out;
  -o-transition: transform .2s ease-in-out;
  transition: transform .2s ease-in-out;
  transition: transform .2s ease-in-out, -webkit-transform .2s ease-in-out;
}

.siderbar-cuenta--abierto {
  -webkit-transform: translateX(0);
      -ms-transform: translateX(0);
          transform: translateX(0);
}


/* HEADER */
.siderbar-header {
  padding: 10px 20px;
  text-align: center;
  background: #F3F8FE;
}
.siderbar-header__icons {
  text-align: right;
}
.siderbar-header__icon {
  color: #777;
  font-size: 20px;
  font-weight: 100;
  margin-top: 10px;
}
.siderbar-header__imagen {
  width: 75px;
  height: 75px;
  border-radius: 50%;
  margin: 0 0 10px 0;
}
.siderbar-header__nombre {
  text-transform: uppercase;
  font-weight: 500;
  margin: 0 0 8px 0;
}
.siderbar-header__descripcion {
  font-weight: 300;
  font-size: 11.5px;
  margin: 0 0 3px 0;
}
.siderbar-header__links {
  margin-top: 14px;
  padding-bottom: 10px;
}
.siderbar-header__mi-cuenta {
  color: #206ec5;
  font-weight: 500;
}
.siderbar-header__salir {
  color: #e4585a;
  font-weight: 500;
  position: relative;
  margin-left: 40px;
}

.siderbar-header__salir:before {
  content: '';
  position: absolute;
  top: 3px;
  left: -22px;
  background:black;
  width: 1.5px;
  height: 12px;
}
.siderbar-header__caret {
  color: #F3F8FE;
  width: 30px;
  font-size: 50px;
  position: absolute;
  right: 326px;
  top: -27px;
}
/* BODY */
.siderbar-body {
  width: 100%;
  height: 100%;
  background: #FFF;
}
.siderbar-body__head {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  padding: 8px 20px;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.siderbar-body__title {
  text-transform: uppercase;
  padding: 0;
  color: #999;
}
.siderbar-body__gestionar {
  color: #206ec5;
  font-weight: 400;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.siderbar-body__gestionar:hover {
  text-decoration: none;
}
.siderbar-body__icon {
  margin-right: 6px;
}

.siderbar-body__organizacion {
  margin: 5px 0;
  padding: 3px 20px;
  display: block;
  font-size: 15px;
  color: black;
  position: relative;
  padding-right: 40px;
  text-decoration: none;
}
.siderbar-body__organizacion .small {
  font-size: 10px;
}
.siderbar-body__organizacion:hover {
  text-decoration: none;
  background: #2e8fda;
  color: #FFF;
}
.siderbar-body__check {
  color: rgb(16, 188, 131);
  position: absolute;
  top: 0;
  bottom: 0;
  right: 10px;
  margin: auto;
}



