.vista-organizacion-lista {
  background: #f4f7f8;
  position: absolute;
  left: 0px;
  right: 0px;
  bottom: 0px;
  top: 0px;
}

.vista-organizacion-lista__vista {
  max-width: 940px;
  margin: 35px auto;
  margin-bottom: 21px;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 2px;
  -webkit-box-shadow: 0 1px 1px rgba(0,0,0,.05);
  box-shadow: 0 1px 1px rgba(0,0,0,.05);
}

.vista-organizacion-lista__header {
  padding: 20px;
  background-color: #e7eced;
  border-color: #cbd8da;
}
.vista-organizacion-lista__body {
  padding: 20px;
}

.vista-organizacion-lista__lista_item {
  border-bottom: 1px solid #eee;
  padding-top: 15px;
  padding-bottom: 15px;
}

.vista-organizacion-lista__imagen {
  max-height: 90px;
  border: 1px dashed #DDD;
  text-align: center;
  padding: 10px;
}

.vista-organizacion-lista__lista_item:last-child {
  border-bottom: 0px;
}

.vista-organizacion-lista__detalle {

}

.vista-organizacion-lista__detalle div {
  margin-bottom: 2px;
  font-weight: 400;
}

.vista-organizacion-lista__detalle div a {
  font-weight: 400;
}

.vista-organizacion-lista__detalle div i {
  color: #777;
}

.vista-organizacion-lista__detalle div:last-child {
  margin-bottom: 0px;
}

.vista-organizacion-lista--success {
  color: #388a10;
}

.landing-organizacion {
  width: 100%;
}

.landing-organizacion__header {
  text-align: center;
  color: #FFF;
  padding: 35px 85px;
  background: -webkit-linear-gradient(30deg,#978BFD,#7768FD);
  background: -o-linear-gradient(30deg,#978BFD,#7768FD);
  background: linear-gradient(60deg,#978BFD,#7768FD);
}

.landing-organizacion__body {
  padding: 20px;
}

.landing-organizacion__footer {
  padding: 15px;
  text-align: center;
}



