.preload {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  text-align: center;
  background: -webkit-linear-gradient(30deg, #978BFD, #7768FD);
  background: -o-linear-gradient(30deg, #978BFD, #7768FD);
  background: linear-gradient(60deg, #978BFD, #7768FD);
}

.preload__title {
  color: white;
  font-size: 18px;
  margin-top: 20%;
}

.preload__loading {
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
}



