.cuenta {
  overflow-x: scroll;
  background-color: #FFF;
  position: relative;
}

.cuenta-header {
  vertical-align: middle;
  width: 100%;
  text-transform: uppercase;
  font-weight: 600;
  -webkit-font-smoothing: antialiased;
  font-size: 1.09rem;
  background: #f5f5f5;
}

.cuenta-header div {
  color: #263035;
  display: inline-block;
  width: 15%;
  padding: 10px 8px 7px 20px;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 12px;
  vertical-align: bottom;
  -webkit-font-smoothing: antialiased;
}

.cuenta-header div:first-child {
  padding-left: 35px;
  width: 18%;
}

.cuenta-header div:last-child {
  width: 1%;
}

.cuenta-header .porcentajediez{
  width: 10%;
}

.cuenta-fila .porcentajediez{
  width: 10%;
}
.cuenta-fila .porcentajetreinta{
  position: relative;
  width: 30%;
}
.cuenta-fila .inline{
  display:-ms-flexbox;
  display:flex;
}

.cuenta-fila {
  width: 100%;
  border-top: 1px solid #e9ecef;
  -webkit-transition: background-color 0.2s linear;
  -o-transition: background-color 0.2s linear;
  transition: background-color 0.2s linear;
  cursor: pointer;
  word-wrap: break-word;
}

.cuenta-fila.active,
.cuenta-fila:hover {
  background-color: #F5F5F5;
}

.cuenta-fila div {
  display: inline-block;
  width: 15%;
  padding: 12px 12px 12px 20px;
  vertical-align: top;
}

.cuenta-fila div:first-child {
  display: inline-block;
  width: 18%;
  position: relative;
  padding-left: 35px;
}

.cuenta-fila div:last-child {
  width: 1%;
}

.cuentas.cuentas-es-solo-una-columna .cuenta-header{
  display: none;
}

.cuentas.cuentas-es-solo-una-columna .cuenta-header div{
  display: none;
}

.cuentas.cuentas-es-solo-una-columna .cuenta-header div:first-child{
  display: block;
  width: 100%;
}

.cuentas.cuentas-es-solo-una-columna .cuenta-body .cuenta .cuenta-fila div{
  display: none;
}

.cuentas.cuentas-es-solo-una-columna .cuenta-body .cuenta .cuenta-fila div:first-child {
  display: block;
  width: 100%;
}



