.page-toolbar {
  position: relative;
  margin-top: 10px;
  margin-bottom: 5px;
  padding: 1px 10px 4px 15px;

}

.page-toolbar__titulo{
  margin: 0px 0px;
  font-weight: 100;
  font-size: 20px;
  line-height: 34px;
  height: 34px;
}

.page-toolbar__titulo--over-flow {
  max-width: 100%;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.section-header-button-toolbar {
  display: inline-block;
  float: right;
  margin-right: 35px;
}

.btn.btn-info.primary
{
  background-color: #988CFF;
  border-color: #988CFF;
}

.page-toolbar__actions {
  float: right;
}

.page-item-sort {
  display: inline-block;
  position: absolute;
  margin-left: 4px;
  margin-top: 1px;
}

.page-item-sort b {
  display: block;
  height: 6px;
  font-size: 10px;
  opacity: 1;
}

.page-item-sortable a {
  color: #262626;
  text-decoration: none;
  background-color: #f5f5f5;
}

.page-item-sortable.asc a .sort-up {
  opacity: 1;
}

.page-item-sortable.asc a .sort-down {
  opacity: .5;
}

.page-item-sortable.desc a .sort-up {
  opacity: .5;
}

.page-item-sortable.desc a .sort-down {
  opacity: 1;
}



