.ayuda {
  width: 400px;
  height: calc(100vh - 50px);
  z-index: 999;
  position: fixed;
  top: 50px;
  right: 0;
  background: #FFF;
  -webkit-box-shadow: -2px 5px 10px 1px rgba(0,0,0,.176);
          box-shadow: -2px 5px 10px 1px rgba(0,0,0,.176);
  -webkit-transform: translateX(450px);
      -ms-transform: translateX(450px);
          transform: translateX(450px);
  -webkit-transition: -webkit-transform .2s ease-in-out;
  transition: -webkit-transform .2s ease-in-out;
  -o-transition: transform .2s ease-in-out;
  transition: transform .2s ease-in-out;
  transition: transform .2s ease-in-out, -webkit-transform .2s ease-in-out;
}
.ayuda--abierto {
  -webkit-transform: translateX(0);
      -ms-transform: translateX(0);
          transform: translateX(0);
}

/* HEADER */
.ayuda__header {
  padding: 12px 24px;
  background: #F3F8FE;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
  border-bottom: 1px solid #ededed;
}
.ayuda__title {
  font-size: 20px;
  font-weight: 400;
  padding: 0;
}
.ayuda__icono {
  color: #777;
  font-size: 20px;
  font-weight: 100;
  margin-top: 10px;
}
.ayuda__caret {
  color: #F3F8FE;
  width: 30px;
  font-size: 50px;
  position: absolute;
  right: 68px;
  top: -27px;
}

/* BUSCADOR */
.buscador {
  width: 100%;
  position: relative;
  padding: 10px 0;
}
.buscador__icon {
  position: absolute;
  top: 50%;
  left: 8px;
  -webkit-transform: translateY(-55%);
      -ms-transform: translateY(-55%);
          transform: translateY(-55%);
}
.buscador__input {
  display: inline-block;
  width: 100%;
  padding-left: 25px;
  height: 33px;
  outline: none;
}

/* BODY */
.ayuda__opcion {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  padding: 3px 20px;
  margin: 5px 0;
  color: black;
  font-size: 14px;
}
.ayuda__opcion-dos {
  padding-top: 3px;
  padding-bottom: 12px; 
  border-bottom: 1px solid #ededed;
}
.ayuda__opcion > p {
  width: 100%;
  font-weight: 300;
  margin: 0;
}
.ayuda__opcion > span {
  font-size: 11px;
}

.ayuda__opcion:hover {
  text-decoration: none;
  color: #fff;
  background-color: #2e8fda;
}
.ayuda__opcion:hover .ayuda__icon-body {
  color: white;
}

