.historial {
  padding: 10px;
  text-decoration: none;
  list-style: none;
}

.historial__item {
  list-style: none;
  text-decoration: none;
  padding-bottom: 5px;
  position: relative;
}

.historial__item::before{
  content: '';
  border-left: 1px solid #E4E4E4;
  position: absolute;
  left: 152px;
  top: 0px;
  bottom: 0px;
}

.historial__item:last-child::before{
  content: '';
  border-left: 1px solid #E4E4E4;
  position: absolute;
  left: 0;
  top: 0;
  height: 0;
}

.historial__fecha {
  display: inline-block;
  width: 140px;
  color: #999;
  font-size: 10px;
  vertical-align: top;
  padding-top: 5px;
}

.historial__texto {
  display: inline-block;
  max-width: 500px;
}

.comentario {

}

.comentario__tipo {
  display: inline-block;
  border: 1px solid #ededed;
  border-radius: 50%;
  height: 25px;
  width: 25px;
  text-align: center;
  line-height: 1.7;
  font-size: 12px;
  color: #777;
  position: relative;
  background: #F6FBFF;
  vertical-align: top;
}

.comentario__tipo--comentado {
  color: #7ab2cc;
}

.comentario__tipo--creado {
  color: #5cb655;
}

.comentario__tipo--actualizado {
  color: #e29a00;
}

.comentario__tipo--eliminado {
  color: #df6869;
}

.comentario__texto {
  display: inline-block;
  padding-left: 10px;
  padding-right: 10px;
  max-width: 475px;
}

.comentario__descripcion {
  display: inline-block;
  font-size: 12px;
  padding-left: 2px;
  padding-right: 2px;
}

.comentario__por {
  display: inline-block;
  color: #777;
  font-size: 11px;
  font-weight: 400;
}

.comentario__por b {
  font-size: 11px;
  padding-left: 3px;
}

.comentario__acciones {
  display: inline-block;
  padding-left: 5px;
  padding-right: 5px;
  font-size: 12px;
}

.comentario__acciones a{
  font-size: 11px;
  color: #777;
  text-decoration: none;
}

.historial_acciones a {
  padding: 0px 10px;
  text-decoration: none;
  font-size: 12px;
}



