.select-custom {
  position: relative;
}

.select-opciones {
  max-height: 160px;
  border: 1px solid #ccc;
  overflow: hidden;
  overflow-y: scroll;
  visibility: hidden;
  position: absolute;
  left: 15px;
  right: 15px;
  z-index: 6;
  background: #FFF;
  border: 1px solid rgba(0,0,0,.15);
  border-radius: 2px;
  -webkit-box-shadow: 0 6px 12px rgba(0,0,0,.175);
  box-shadow: 0 6px 12px rgba(0,0,0,.175);
  background-clip: padding-box;
  width: 100%;
  left: 0;
}

.select-opciones.active {
  visibility: visible;
}

.select-opciones-nuevo {
  border-top: 1px solid #CCC;
  white-space: nowrap;
  text-decoration: none !important;
  display: block;
  width: 100%;
  padding: 5px 10px;
  color: #206ec5;
}



