.autocomplete{
  position: relative;
}
.autocomplete input {
  color: #555;
  font-size: 14px;
  width: 100%;
  padding: 0 0 0 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-weight: 500;
  outline: none;
  -webkit-box-shadow: none !important;
          box-shadow: none !important;
  font-weight: 300;
  padding: 6px 25px 6px 12px;
  height: 34px;
}

.autocomplete input::-webkit-input-placeholder {
  font-weight: 300;
  color: #99999E;
}

.autocomplete input:-ms-input-placeholder {
  font-weight: 300;
  color: #99999E;
}

.autocomplete input::-ms-input-placeholder {
  font-weight: 300;
  color: #99999E;
}

.autocomplete input::placeholder {
  font-weight: 300;
  color: #99999E;
}

.autocomplete input:focus {
  border-color: #66afe9;
  -webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px rgba(102,175,233,.6);
  box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px rgba(102,175,233,.6);
}

.autocomplete-icons {
  position: absolute;
  right: 10px;
  top: 2px;
  font-size: 20px;
  color: #aaa;
}

.autocomplete-icons a {
  text-decoration: none;
  color: inherit !important;
  font-size: 20px;
}

.autocomplete-body {
  border: 1px solid rgba(0,0,0,.15);
  border-radius: 2px;
  -webkit-box-shadow: 0 6px 12px rgba(0,0,0,.175);
  box-shadow: 0 6px 12px rgba(0,0,0,.175);
  background-clip: padding-box;
  position: absolute;
  width: 100%;
  background-color: #FFF;
  z-index: 1;
  max-height: 240px;
  overflow-y: scroll;
  display: none;
}

.autocomplete-body.is-open {
  display: block;
}

.autocomplete-body ul,
.autocomplete-body li {
  margin: 0;
  padding: 0;
  list-style: none;
}

.autocomplete-body li a {
  display: block;
  text-decoration: none;
  padding: 5px 10px;
  color: #29364D !important;
}

.autocomplete-body li a:hover,
.autocomplete-body li a.active,
.autocomplete-body li a.selected {
  background: #cbe4ff9f;
}



