.select-search {
  position: relative;
}
.select-searched {
  color: #555;
  font-size: 14px;
  width: 100%;
  line-height: 33px;
  padding: 0 0 0 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-weight: 500;
  outline: none;
  padding-right: 50px;
  font-weight: 300;
}

.select-searched--solo-texto {
  line-height: 15px;
  padding: 9px 50px 9px 10px;
}

.select-searched::-webkit-input-placeholder {
  font-weight: 400;
}

.select-searched:-ms-input-placeholder {
  font-weight: 400;
}

.select-searched::-ms-input-placeholder {
  font-weight: 400;
}

.select-searched::placeholder {
  font-weight: 400;
}
.select-icons {
  position: absolute;
  top: 18px;
  right: 8px;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  z-index: 4;
}
.select-icons > a {
  font-size: 20px;
  margin: 0 3px;
  color: #aaa;
  cursor: pointer;
  text-decoration: none;
  line-height: 14px;
  vertical-align: middle;
}
.select-icons > a.x {
  font-size: 14px;
}
.select-icons > a.x:hover {
  color: red;
}



