
.producto {
  overflow-x: scroll;
  background-color: #FFF;
  position: relative;
}

.producto-header {
  vertical-align: middle;
  width: 100%;
  text-transform: uppercase;
  font-weight: 600;
  -webkit-font-smoothing: antialiased;
  font-size: 1.09rem;
  background: #f5f5f5;
}

.producto-header div {
  color: #263035;
  display: inline-block;
  width: 20%;
  padding: 10px 8px 7px 20px;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 12px;
  vertical-align: bottom;
  -webkit-font-smoothing: antialiased;
}
.producto-header div:first-child {
  padding-left: 35px;
  width: 30%;
}
.producto-header div:last-child {
  width: 10%;
}


.producto-fila {
  width: 100%;
  border-bottom: 1px solid #e9ecef;
  -webkit-transition: background-color 0.2s linear;
  -o-transition: background-color 0.2s linear;
  transition: background-color 0.2s linear;
  cursor: pointer;
}

.producto-fila.active,
.producto-fila:hover {
  background-color: #f9f9f9;
}

.producto-fila div {
  display: inline-block;
  width: 20%;
  padding: 12px 12px 12px 20px;
  vertical-align: top;
}

.producto-fila div:first-child {
  display: inline-block;
  width: 30%;
  position: relative;
  padding-left: 35px;
}
.producto-fila div:last-child {
  width: 10%;
}

.productos.productos-es-solo-una-columna .producto-header{
  display: none;
}

.productos.productos-es-solo-una-columna .producto-header div{
  display: none;
}

.productos.productos-es-solo-una-columna .producto-header div:first-child{
  display: block;
  width: 100%;
}

.productos.productos-es-solo-una-columna .producto-body .producto .producto-fila div{
  display: none;
}

.productos.productos-es-solo-una-columna .producto-body .producto .producto-fila div:first-child {
  display: block;
  width: 100%;
}



