.producto-detalle {
  padding-top: 20px;
}

.importacion__upload {
  display: inline-block;
  vertical-align: middle;
  margin-right: 5px;
}

.importacion__body {
  padding: 5px 25px 30px;
}

.importacion__preview {
  display: block;
  width: 100%;
}

.importacion__preview_seccion {
  display: block;
  width: 100%;
}

.importacion__preview_title {
  padding: 5px 15px;
  padding-bottom: 10px;
  border-bottom: 1px solid #ededed;
}

.importacion__preview_table {
  width: 100%;
  margin-bottom: 20px;
}

.importacion__preview_table tr td {
  padding: 8px;
}

.importacion__preview_table tr:hover td {
  background-color: #f5f5f5;
}

.importacion__preview_lista {
  list-style: none;
}
.importacion__preview_body {
  padding-left: 40px;
}
.importacion__preview_lista li {
  width: 100%;
}



