.preferencias-vista {
  position: relative;
  overflow: hidden;
  display: -ms-flexbox;
  display: flex;
}

.preferencias-vista__sidebar {
  width: 20%;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  border-right: 1px solid #c7c6c7;
  height: calc(100vh - 50px);
}

.preferencias-vista__escenas {
  width: 80%;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.preferencias-vista__titulo {
  padding-left: 15px;
  padding-right: 15px;
  font-weight: 300;
  font-size: 28px;
  padding-top: 15px;
  padding-bottom: 25px;
}

.preferencias-vista__menu {
  list-style: none;
  margin: 0;
  padding: 0;
}

.preferencias-vista__menu a{
  display: inline-block;
  width: 100%;
  text-decoration: none;
  padding: 6px 20px;
  border-top: 1px solid transparent;
  border-bottom: 1px solid transparent;
  background: inherit;
  color: #333;
  font-weight: 300;
  font-size: 15px;
}

.preferencias-vista__menu a:hover{
  text-decoration: none;
  background: #f7f7f7;
}

.preferencias-vista__menu a.active,
.preferencias-vista__menu li.active a{
  border-top-color: #ddd;
  border-bottom-color: #ddd;
}

.preferencias-vista__header {
  font-size: 28px;
  padding: 15px;
  font-weight: 400;
}

.preferencias-vista__table {
  width: 100%;
}

.preferencias-vista__table thead {
  font-size: 12px;
  text-transform: uppercase;
}
.preferencias-vista__table thead > tr > th {
  border-top: 1px solid #e6e6e6;
  border-bottom: 1px solid #e6e6e6;
  color: #263035;
  background-color: #f7f7f7;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  padding: 8px;
}

.preferencias-vista__table tbody > tr > td {
  padding: 8px;
  line-height: 16px;
  vertical-align: top;
  border-top: 1px solid #ededed;
  color: #222;
}


.preferencias-vista__table tbody > tr > td .campos-personalizado__accion {
  font-size: 12px;
  text-decoration: none;
  color: #222;
  font-weight: 400;
}

.preferencias-vista__table tbody > tr:hover > td {
  background-color: #fafafa;
}

.preferencias-vista__table tbody > tr > td .campos-personalizado__accion-grupo {
  display: none;
}

.preferencias-vista__table tbody > tr:hover > td .campos-personalizado__accion-grupo {
  display: inline-block;
}

.preferencias-vista__table .empty-list {
  padding: 80px 40px;
  vertical-align: middle;
  text-align: center;
  color: #777;
}

.preferencias-general {
  padding: 10px 30px;
}



