
.Menu {
  width: 200px;
  border-right: 1px solid #e4eaec;
  height: calc(100vh - 50px);
  background: #333F66;
  -webkit-transition: all .2s ease;
  -o-transition: all .2s ease;
  transition: all .2s ease;
  margin-top: 50px;
  position: fixed;
}
.Menuesconder {
  margin-left: -200px;
}

/* MENU-LIST */
hr {
  border: 1px solid #3B4648;
  margin: 15px auto 0 auto;
  width: 150px;
}
h5 {
  padding: 25px 0 12px 25px;
}
.ulMenu {
  list-style: none;
  padding-left: 0;
  width: 100%;
}
.ulMenu > li > a > .icon-left {
  margin-right: 13px;
  color: #ffffff;
  width: 18px;
}
.ulMenu > li > a:hover > .icon-left {
  color: white;
}
.ulMenu > li > a > .icon-right {
  float: right;
  font-size: 18px;
}

.ulMenu > li > a {
  display: inline-block;
  text-decoration: none;
  color: white;
  width: 100%;
  padding: 10px 25px 10px 20px;
}

.ulMenu > li > a.active {
  background: #212943;
  border-left: 4px solid #978BFD;
  padding-left: 16px;
  color: #D2CDFE;
}
.ulMenu > li > a.active > .icon-left {
  color: white;
}

.ulMenu > li > a:hover {
  background: #7768FD;
}

.MenuList .MenuList {
  padding-left: 18px;
}
.MenuList .MenuList .ulMenu > li > a {
  border-left: 1px solid #536c79;
  position: relative;
}
.MenuList .MenuList .ulMenu > li > a:after {
  content: '';
  display: block;
  width: 13px;
  height: 1px;
  background: #536c79;
  position: absolute;
  left: 0px;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
}
.MenuList .MenuList .ulMenu > li > a:hover:after {
  background: #20a8d8;
}

.SpaceMenuList
{
  height:  18px;
}



