.opciones {
  padding-left: 0;
  list-style: none;
  margin-bottom: 0;
}
.ocultar {
  display: none;
}
.mostrar {
  display: block;
}

.opciones > li a {
  padding-left: 10px;
  cursor: pointer;
  min-height: 26px;
  line-height: 35px;
  text-decoration: none;
  width: 100%;
  display: inline-block;
  color: #29364D;
  position: relative;
  line-height: 16px;
  padding-top: 5px;
  padding-bottom: 5px;
}

.opciones > li a:hover,
.opciones > li a.active {
  background: #cbe4ff9f;
}

.opciones li ul {
  margin-left: 15px;
  border-left: 1px solid #CCC;
}

.opciones li li a::before {
  content: '';
  width: 8px;
  height: 1px;
  position: absolute;
  background: #CCC;
  left: 0px;
  top: 0px;
  bottom: 0px;
  margin: auto;
}



