.rol-formulario {
  padding: 15px;
}

.rol-formulario__table {
  width: 100%;
  color: #222;
  font-size: 15px;
  font-weight: 400;
}
.rol-formulario__table thead > tr > th,
.rol-formulario__table tbody > tr > td {
  border: 1px solid #ededed;
  font-size: 14px;
  font-weight: 400;
}

.rol-formulario__table thead > tr > th:nth-child(2),
.rol-formulario__table tbody > tr > td:nth-child(2) {
  background-color: #f5f5f5;
}

.rol-formulario__table thead > tr > th {
  vertical-align: middle;
  text-align: center;
  padding: 8px;
}

.rol-formulario__table tbody > tr > td {
  vertical-align: middle;
  text-align: center;
  padding: 8px;
}

.rol-formulario__table tbody > tr > td.text-left {
  text-align: left;
}

.rol-formulario__mas_permisos {
  list-style: none;
}



