.factura-vista {
  position: relative;
}

.factura-vista__detalle {
  position: relative;
  border: 1px solid #eee;
  margin-bottom: 25px;
  margin: 35px;
  max-width: 820px;
}

.cinta {
  position: absolute !important;
  top: -5px;
  left: -5px;
  width: 96px;
  height: 94px;
  border-bottom-right-radius: 92px;
  overflow: hidden;
}

.cinta__interno {
  text-align: center;
  color: #FFF;
  top: 24px;
  left: -31px;
  width: 135px;
  padding: 3px;
  position: relative;
  -webkit-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

.cinta__interno::before,
.cinta__interno::after {
  content: "";
  border-top: 5px solid transparent;
  border-left: 5px solid;
  border-left-color: inherit;
  border-right: 5px solid transparent;
  border-bottom: 5px solid;
  border-bottom-color: inherit;
  position: absolute;
  top: 20px;
  -webkit-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

.cinta__interno::before {
  left: 0;
  border-left: 2px solid transparent;
}

.cinta__interno::after {
  right: -2px;
  border-bottom: 3px solid transparent;
}

.cinta__interno--borrador {
  background: #94a5a6;
  border-color: #788e8f;
}



