.dashboard-section {
  padding: 30px 35px 24px 35px;
}

.dashboard-section:first-child {
  margin-top: 0;
}

.actividad-ventas {
  width: 100%;
}

.actividad-ventas__titulo {
  font-size: 20px;
  padding-bottom: 10px;
}

.actividad-ventas__body {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.actividad-ventas__info {
  display: inline-block;
  margin-right: 15px;
  padding: 28px 5px 7px;
  height: 123px;
  vertical-align: top;
  border: 1px solid #DAE1E4;
  border-radius: 7px;
  background-color: #fff;
  text-align: center;
  width: 100%;
}

.actividad-ventas__info:hover {
  border-color: #978BFD;
}

.ventas-info {
  display: block;
  text-align: center;
  text-decoration: none !important;
}

.ventas-info__total {
  font-size: 36px;
  color: #1093de;
}
.ventas-info__body {
  line-height: 16px;
}

.ventas-info__unidad {
  font-size: 8px;
  color: #8d99ae;
}

.actividad-ventas__etiqueta {
  color: #495569;
  font-size: 12px;
  text-transform: uppercase;
}

/* dashboard__widget */
.dashboard__widget {
  border: 1px solid #ECECEC;
  padding: 15px 12px 0px;
}

.dashboard__widget-header {
  width: 100%;
  border-bottom: 1px solid #EDEDED;
  padding: 0 10px;
}

.dashboard__widget-title {
  font-size: 15px;
  margin-bottom: 10px;
  text-transform: uppercase;
  font-weight: 400;
}

.dashboard__widget-opciones {
  float: right;
}

.dashboard__widget-opcion {
  border: none!important;
  background: none!important;
  outline: none!important;
  -webkit-box-shadow: none!important;
          box-shadow: none!important;
  padding: 0;
}

.top-ventas {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.top-ventas__item {
  width: 100%;
  text-align: center;
  padding: 20px 0;
  min-height: 112px;
}

.top-ventas__item:not(:first-child) {
  border-left: 1px solid #ececec;
}

.top-ventas__item-image {
  font-size: 75px;
}

.top-ventas__item-detalle {
  text-decoration: none !important;
  display: block;
  padding-bottom: 10px;
}

.top-ventas__nombre {
  max-width: 100%;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
  color: #666;
}

.top-ventas__cantidad {
  font-size: 24px;
  color: #222;
}

.opciones-simples {
  list-style: none;
}

.opciones-simples a {
  text-decoration: none;
  display: block;
  padding: 2px 10px 2px 15px;
  color: #222;
  background: #FFF;
}

.opciones-simples a:hover,
.opciones-simples a:active,
.opciones-simples a.active {
  background: #978BFD;
  color: #FFF;
}

.dashboard-table {
  width: 100%;
  max-width: 100%;
}

.dashboard-table thead tr th {
  font-size: 14px;
  background-color: #F7F9FB;
  padding: 8px;
  font-weight: 400;
  text-align: center;
}

.dashboard-table thead tr th:first-child {
  padding-left: 15px;
  text-align: left;
}

.dashboard-table tbody tr td {
  color: #666;
  padding: 13px 15px;
  text-align: center;
}

.dashboard-table tbody tr td:first-child {
  text-align: left;
}

.resumen_cotizaciones,
.resumen_ordenes_venta,
.cotizacion_grafico,
.orden_venta_grafico {
  position: relative;
}

.popover-container {
  position: relative;
}
.popover-custom {
  position: absolute;
  right: 114%;
  top: -10px;
  bottom: -21px;
  background: white;
  padding: 15px;
  border: 1px solid rgba(0,0,0,.2);
  -webkit-box-shadow: 0 5px 10px rgba(0,0,0,.2);
          box-shadow: 0 5px 10px rgba(0,0,0,.2);
  border-radius: 4px;
}

.popover-custom__fechas {
  white-space: nowrap;
}



