
.orden-venta {
  overflow-x: scroll;
  background-color: #FFF;
  position: relative;
}

.orden-venta-header {
  vertical-align: middle;
  width: 100%;
  text-transform: uppercase;
  font-weight: 600;
  -webkit-font-smoothing: antialiased;
  font-size: 1.09rem;
  background: #f5f5f5;
}

.orden-venta-header div {
  color: #263035;
  display: inline-block;
  width: 11.1%;
  padding: 10px 8px 7px 20px;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 12px;
  vertical-align: bottom;
  -webkit-font-smoothing: antialiased;
}
.orden-venta-header div:first-child {
  display: none;
}


.orden-venta-fila {
  width: 100%;
  border-top: 1px solid #e9ecef;
  -webkit-transition: background-color 0.2s linear;
  -o-transition: background-color 0.2s linear;
  transition: background-color 0.2s linear;
  cursor: pointer;
}

.orden-venta-fila .orden-venta-columna {
  display: inline-block;
  width: 11.1%;
  padding: 12px 12px 12px 20px;
  vertical-align: top;
}

.orden-venta-fila .orden-venta-columna:first-child {
  display: none;
}

.orden-venta-fila.active,
.orden-venta-fila:hover {
  background-color: #f5f5f5;
}

.orden-venta.orden-venta-es-solo-una-columna .orden-venta-header{
  display: none;
}

.orden-venta.orden-venta-es-solo-una-columna .orden-venta-header div{
  display: none;
}

.orden-venta.orden-venta-es-solo-una-columna .orden-venta-header div:first-child{
  display: block;
  width: 100%;
}

.orden-venta.orden-venta-es-solo-una-columna .orden-venta-body .orden-venta .orden-venta-fila .orden-venta-columna{
  display: none;
}

.orden-venta.orden-venta-es-solo-una-columna .orden-venta-body .orden-venta .orden-venta-fila .orden-venta-columna:first-child {
  display: block;
  width: 100%;
}

.orden-venta-lista-principal,
.orden-venta-lista-secundaria {
  word-wrap: break-word;
}

.orden-venta-lista-principal span,
.orden-venta-lista-secundaria span {
  float: right;
}

.orden-venta-lista-principal {
  margin-bottom: 5px;
}
.orden-venta-lista-principal span {
  color: #222;
  font-size: 15px;
}
.orden-venta-lista-secundaria {
  color: #777;
}
.orden-venta-lista-secundaria span {
  color: #999;
  font-size: 11px;
}

.orden-venta__estado--borrador {
  color: #999 !important;
  text-transform: uppercase;
  font-size: 11px;
}

.orden-venta__estado--confirmado {
  color: #1093de !important;
  text-transform: uppercase;
  font-size: 11px;
}

.orden-venta__estado--cerrado {
  color: #388a10 !important;
  text-transform: uppercase;
  font-size: 11px;
}

.orden-venta__estado--anulado {
  color: #666 !important;
  text-transform: uppercase;
  font-size: 11px;
}



