
.cotizacion {
  overflow-x: scroll;
  background-color: #FFF;
  position: relative;
}

.cotizacion-header {
  vertical-align: middle;
  width: 100%;
  text-transform: uppercase;
  font-weight: 600;
  -webkit-font-smoothing: antialiased;
  font-size: 1.09rem;
  background: #f5f5f5;
}

.cotizacion-header div {
  color: #263035;
  display: inline-block;
  width: 11.1%;
  padding: 10px 8px 7px 20px;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 12px;
  vertical-align: bottom;
  -webkit-font-smoothing: antialiased;
}
.cotizacion-header div:first-child {
  display: none;
}


.cotizacion-fila {
  width: 100%;
  border-top: 1px solid #e9ecef;
  -webkit-transition: background-color 0.2s linear;
  -o-transition: background-color 0.2s linear;
  transition: background-color 0.2s linear;
  cursor: pointer;
}

.cotizacion-fila .cotizacion-columna {
  display: inline-block;
  width: 11.1%;
  padding: 12px 12px 12px 20px;
  vertical-align: top;
}

.cotizacion-fila .cotizacion-columna:first-child {
  display: none;
}

.cotizacion-fila.active,
.cotizacion-fila:hover {
  background-color: #f5f5f5;
}

.cotizacion.cotizacion-es-solo-una-columna .cotizacion-header{
  display: none;
}

.cotizacion.cotizacion-es-solo-una-columna .cotizacion-header div{
  display: none;
}

.cotizacion.cotizacion-es-solo-una-columna .cotizacion-header div:first-child{
  display: block;
  width: 100%;
}

.cotizacion.cotizacion-es-solo-una-columna .cotizacion-body .cotizacion .cotizacion-fila .cotizacion-columna{
  display: none;
}

.cotizacion.cotizacion-es-solo-una-columna .cotizacion-body .cotizacion .cotizacion-fila .cotizacion-columna:first-child {
  display: block;
  width: 100%;
}

.cotizacion-lista-principal,
.cotizacion-lista-secundaria {
  word-wrap: break-word;
}

.cotizacion-lista-principal span,
.cotizacion-lista-secundaria span {
  float: right;
}

.cotizacion-lista-principal {
  margin-bottom: 5px;
}
.cotizacion-lista-principal span {
  color: #222;
  font-size: 15px;
}
.cotizacion-lista-secundaria {
  color: #777;
}
.cotizacion-lista-secundaria span {
  color: #999;
  font-size: 11px;
}

.cotizacion__estado--borrador {
  color: #999 !important;
  text-transform: uppercase;
  font-size: 11px;
}

.cotizacion__estado--confirmado {
  color: #1093de !important;
  text-transform: uppercase;
  font-size: 11px;
}

.cotizacion__estado--cerrado {
  color: #388a10 !important;
  text-transform: uppercase;
  font-size: 11px;
}

.cotizacion__estado--anulado {
  color: #666 !important;
  text-transform: uppercase;
  font-size: 11px;
}


.cotizacion-columna--acciones {
  position: relative;
  padding-left: 25px;
}

.cotizacion-columna--accion .checkbox {
  margin: 0;
}
.cotizacion-columna--accion {
  position: absolute;
  left: 5px;
  bottom: 0px;
  top: 0px;
  margin: auto;
  width: 20px;
  height: 20px;
}



