.popover-documentos {
  display: block;
  width: 240px;
}

.popover-documentos__item {
  padding: 10px 0 6px;
  border-bottom: 1px solid #EDEDED;
}

.popover-documentos__item:last-child {
  border-bottom: 0px;
}

.popover-documentos__body {
  display: block;
}

.popover-documentos__name {
  max-width: 85%;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.popover-documentos__name a {
  text-decoration: none;
  color: #428bca;
  font-size: 12px;
  font-weight: 400;
}

.popover-documentos__file-size {
  font-size: 11px;
  color: #777;
}

.popover-documentos__actions {
  float: right;
}

.popover-documentos__actions a {
  text-decoration: none;
  font-size: 11px;
  color: #333;
}

.popover-documentos__files {
  width: 100%;
  padding: 5px 5px;
  cursor: pointer;
  border: 1px dashed #cfcfcf;
  border-radius: 5px;
}

.popover-documentos__files a {
  font-size: 12px;
  font-weight: 400;
  color: #428bca;
  display: inline-block;
  width: 100%;
  text-decoration: none;
}

.popover-documentos__files span {
  font-size: 11px;
  display: inline-block;
  width: 100%;
  font-size: 11px;
  color: #777;
}



