.comentario__container{
  position: relative;
  padding: 0px 100px 0px 100px;

}

.comentario-tree {
  margin: 50px 28px 100px 130px;
}

.tree-structure {
  border-left: 1px solid #978CFF;
}

.comentario-tree::before {

}

.comentario-nodo {

  padding: 10px 0;
  position: relative;
  margin-left: 0px;
}
.comentario-formulario
{
  padding-top: 10px;
  height: 170px;
}


.comentario-nodo::before {
  content: ' ';
  position: absolute;
  display: block;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  top: 30px;
  left: -6px;
  background-color: #978CFF;
}

.comentario-nodo-time {
  font-weight: 400;
  position: absolute;
  top: 25px;
  width: 90px;
  text-align: right;
  left: -120px;
  font-size: 86%;
  color: #777;
  line-height: 1.6;
}

.comentario-nodo-leaf {
  padding: 10px 20px;
  position: relative;
  margin-bottom: 10px;
  margin-left: 25px;
  border: 1px solid #efefef;
  background-color: #fff;
  border-radius: 6px;

}

.comentario-flecha::after {
  position: absolute;
  display: block;
  width: 0;
  height: 0;
  border-color: transparent #fff transparent transparent;
  border-style: solid;
  border-width: 10px 10px 10px 0;
  content: " ";
  left: 1px;
  bottom: -10px;
}

.comentario-flecha {
  top: 15px;
  left: -11px;
  position: absolute;
  width: 0;
  height: 0;
  border-color: transparent #f3f3f3 transparent transparent;
  border-style: solid;
  border-width: 11px 11px 11px 0;
}

.comentario-delete-action{
  position: absolute;
  right: 10px;
  color: #777;
  cursor: pointer;
}

.comentario-transaccion {
  padding-bottom: 5px;
  font-weight: 400;
}

.comentario-text
{
  font-weight: 400;
  padding-right: 10px;
  margin-bottom: 5px;
}

.comentario-comentado{
  color: #777;
}


