.cotizacion-vista {
  position: relative;
  overflow: hidden;
}
.cotizacion-numero {
  margin-bottom: 15px;
}
.cotizacion-info {
  padding: 20px 0 30px;
}
.cotizacion-detalle {
  padding-top: 6px;
  font-size: 13px;
}
.cotizacion-direccion {
  font-size: 13px;
  padding-top: 12px;
  line-height: 18px;
}

.cotizacion-items {
  width: 100%;
  margin-bottom: 15px;
}
.cotizacion-items thead {
  color: #666;
}
.cotizacion-items thead th {
  border: 1px solid #dadada;
  background: #f9f9f9;
  padding: 8px 10px;
  vertical-align: top;
  font-size: 12px;
  text-transform: uppercase;
  font-weight: 400;
}

.cotizacion__section-costos--fila {
  padding-bottom: 6px;
}

.cotizacion__section-costos--final {
  padding-bottom: 6px;
  border-bottom: 1px solid #eee;
  margin-bottom: 6px;
}

.cotizacion-items tbody td {
  border-bottom: 1px solid #dadada;
  padding: 15px;
}

.cotizacion-items tr td:nth-child(4) {
  background: #fafafa;
}

.order-venta-estado--borrador {
  background: #999;
}
.order-venta-estado--confirmado {
  background: #1093de;
}
.order-venta-estado--cerrado {
  background: #388a10;
}
.order-venta-estado--cancelado {
  background: #666;
}



