.reportes {
  padding-left: 20px;
  padding-right: 20px;
}

.nav-reportes {
  list-style: none;
  margin: 0;
  padding: 0;
}

.reporte-body {
  margin-left: -20px;
  margin-right: -20px;
}

.nav-reportes a {
  display: inline-block;
  text-decoration: none !important;
  padding-top: 3px;
  padding-bottom: 3px;
}

.nav-reporte {
  list-style: none;
  margin: 0;
  padding: 0;
  background: #fafafa;
  margin-left: -20px;
  margin-right: -20px;
  height: 34px;
}

.nav-reporte li {
  display: inline-block;
  float: left;
}

.nav-reporte li:first-child a {
  min-width: 250px;
}

.nav-reporte li .menu-item {
  text-decoration: none;
  display: inline-block;
  padding: 5px 15px;
  color: #454545;
  border: 1px solid #eee;
}

.reporte-tabla {
  width: 100%;
  border-bottom: 1px solid #ededed;
}

.reporte-tabla thead tr th {
  background-color: #f7f7f7;
  border-bottom: 1px solid #e6e6e6;
  border-top: 1px solid #e6e6e6;
  padding: 6px;
  color: #263035;
}

.reporte-tabla thead tr th:first-child {
  padding-left: 20px;
}

.reporte-tabla tbody tr td {
  border-top: 1px solid #ededed;
  vertical-align: top;
  padding: 6px;
}

.reporte-tabla tbody tr td {
  padding: 6px;
}

.reporte-tabla tbody tr td:first-child {
  padding-left: 20px;
}



