.alerta {
  position: fixed;
  top: 0;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  display: -ms-flexbox;
  display: flex;
  padding: 2px 15px;
  height: 30px;
  border-radius: 0 0 8px 8px;
  color: white;
  z-index: 99;
}

.ocultar-alerta {
  opacity: 0;
  visibility: hidden;
  -webkit-transition: all .8s;
  -o-transition: all .8s;
  transition: all .8s;
}
.mostrar-alerta {
  opacity: 1;
  visibility: visible;
}
.info {
  background: #62c2df;
}
.success {
  background: #51bc75;
}
.warning {
  background: #fec108;
}
.danger {
  background: #f86c6b;
}
.alerta > .icon-alerta{
  font-size: 20px;
  padding: 2px 5px;
}
.alerta > p {
  line-height: 25px;
}



