.section-header {
  background: white;
  position: relative;
  padding: 15px 10px;
}

.section-header-title {
  margin: 0px 0px;
  font-weight: 100;
  line-height: 34px;
}
.section-header-close {
  display: inline-block;
  position: absolute;
  top: 15px;
  right: 15px;
  text-decoration: none;
  color: #536c79;
}
.section-header-close:hover {
  color: #536c79;
}



