.perfil-org_adv__contacto
{
  border-right: solid 1px #ededed;
}
.perfil-org_adv__contacto-nombre{
  font-weight: 400;
}

.perfil-org_adv__link-config-email
{
  padding-top: 10px;
}


.perfil-org_adv__email--color
{
  color: #f26c4f;
}

.perfil-org_adv__panel{
  padding: 0px;
  margin: 0px;
}
.perfil-org_adv__panel .panel-body {
  padding: 0px;

}

.perfil-org_adv__header {
  background: white;
  position: relative;
  padding: 15px 10px;
  border-bottom: 1px solid #ededed;
  margin-bottom: 30px;
}
  
.perfil-org_adv__header-title {
  margin: 0px 0px;
  font-weight: 100;
  line-height: 34px;
}

.separationline {
  border-left: 1px solid #CCC;
  padding-left: 8px;
}

.font-md {
  font-size: 18px;
}
.perfil-org_nombre-portal
{
  text-transform: lowercase;
}


