.lista-precios {
  overflow-x: scroll;
  background-color: #FFF;
  position: relative;
}

.lista-precios__header {
  font-weight: 600;
  vertical-align: middle;
  width: 100%;
  background: #ECEFF1;
  border-top: 1px solid #e6e6e6;
}

.lista-precios__header div {
  color: #263035;
  display: inline-block;
  width: 15%;
  padding: 10px 8px 7px 20px;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 12px;
  vertical-align: bottom;
  -webkit-font-smoothing: antialiased;
}

.lista-precios__header .porcentajeveinte{
  width: 20%;
}

.lista-precios__header .porcentajediez{
  width: 10%;
}

.lista-precios__fila .porcentajediez{
  width: 10%;
}

.lista-precios__fila .porcentajeveinte{
  width: 20%;
}

.lista-precios__fila .porcentajetreinta{
  position: relative;
  width: 46%;
  vertical-align: top;
}


.lista-precios__fila {
  width: 100%;
  border-top: 1px solid #e9ecef;
  -webkit-transition: background-color 0.2s linear;
  -o-transition: background-color 0.2s linear;
  transition: background-color 0.2s linear;
  cursor: pointer;
  word-wrap: break-word;
}
.lista-precios__txt-info
{
  font-style: italic;
}

.lista-precios__fila.active,
.lista-precios__fila:hover {
  background-color: #F5F5F5;
}

.lista-precios__fila div {
  display: inline-block;
  width: 15%;
  padding: 12px 12px 12px 20px;
  vertical-align: top;
}


