.notificaciones {
  width: 400px;
  height: calc(100vh - 50px);
  position: fixed;
  background: #FFF;
  top: 50px;
  right: 0;
  z-index: 997;
  -webkit-box-shadow: -2px 5px 10px 1px rgba(0,0,0,.176);
          box-shadow: -2px 5px 10px 1px rgba(0,0,0,.176);
  -webkit-transform: translateX(450px);
      -ms-transform: translateX(450px);
          transform: translateX(450px);
  -webkit-transition: -webkit-transform .2s ease-in-out;
  transition: -webkit-transform .2s ease-in-out;
  -o-transition: transform .2s ease-in-out;
  transition: transform .2s ease-in-out;
  transition: transform .2s ease-in-out, -webkit-transform .2s ease-in-out;
}
.notificaciones--abierto {
  -webkit-transform: translateX(0);
      -ms-transform: translateX(0);
          transform: translateX(0);
}

/* HEADER */
.notificaciones__header {
  padding: 12px 24px;
  background: #F3F8FE;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
  border-bottom: 1px solid #ededed;
}
.notificaciones__title {
  font-size: 20px;
  font-weight: 400;
  padding: 0;
}
.notificaciones__icon {
  color: #777;
  font-size: 20px;
  font-weight: 100;
  margin-top: 10px;
}
.notificaciones__caret {
  color: #F3F8FE;
  width: 30px;
  font-size: 50px;
  position: absolute;
  right: 163px;
  top: -27px;
}

/* BODY */
.notificaciones__imagen {
  width: 30px;
  margin: 0 20px;
}
.notificaciones__notificacion {
  padding: 14.5px 5px;
  border-bottom: 1px solid #ededed;
  display: block;
  color: black;
}
.notificaciones__notificacion:hover {
  text-decoration: none;
}


