.select-detail__header {
  position: relative;
}

.select-detail__header-titulo {
  text-transform: uppercase;
  color: #777;
  font-weight: 400;
}

.select-detail__header a {
  text-decoration: none;
  font-size: 10px;
  font-weight: 500;
}

.select-detail__body,
.select-detail__body b,
.select-detail__body div {
  font-size: 12px;
  line-height: 18px;
  color: #777;
}

.select-detail__body a {
  text-decoration: none;
  font-size: 12px;
}

.select-detail__options {
  text-decoration: none;
  list-style: none;
  position: absolute;
  width: 350px;
  z-index: 3;
  border: 1px solid #ccc;
  border: 1px solid rgba(0,0,0,.15);
  border-radius: 2px;
  max-height: 200px;
  overflow-y: scroll;
}

.select-detail__options ul,
.select-detail__options li {
  list-style: none;
  background: white;
  border-bottom: 1px solid #ddd;
  margin-bottom: 0;
}

.select-detail__options li a {
  text-decoration: none !important;
  color: #333;
  padding: 10px 15px;
  display: inline-block;
  width: 100%;
  position: relative;
}

.select-detail__options li a:hover {
  background: #E8E8E8;
  color: #000;
}
.select-detail__options li.selected,
.select-detail__options li.selected a:hover {
  background: #F0F7FB;
}

.select-detail__editar-item {
  display: inline-block;
  width: 14px;
  height: 14px;
  position: absolute;
  right: 15px;
  top: 10px;
}

.select-detail__header-footer a {
  display: inline-block;
  width: 100%;
  background: white;
  font-size: 14px;
  font-weight: 400;
  padding: 5px 15px;
}



