
.factura {
  overflow-x: scroll;
  background-color: #FFF;
  position: relative;
}

.factura__header {
  vertical-align: middle;
  width: 100%;
  text-transform: uppercase;
  font-weight: 600;
  -webkit-font-smoothing: antialiased;
  font-size: 1.09rem;
  background: #f5f5f5;
}

.factura__header div {
  color: #263035;
  display: inline-block;
  width: 12.5%;
  padding: 10px 8px 7px 20px;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 12px;
  vertical-align: bottom;
  -webkit-font-smoothing: antialiased;
}

.factura__header div:first-child {
  display: none;
}


.factura-fila {
  width: 100%;
  border-top: 1px solid #e9ecef;
  -webkit-transition: background-color 0.2s linear;
  -o-transition: background-color 0.2s linear;
  transition: background-color 0.2s linear;
  cursor: pointer;
}

.factura-fila .factura-columna {
  display: inline-block;
  width: 12.5%;
  padding: 12px 12px 12px 20px;
  vertical-align: top;
}

.factura-fila .factura-columna:first-child {
  display: none;
}

.factura-fila.active,
.factura-fila:hover {
  background-color: #f5f5f5;
}

.factura.factura-es-solo-una-columna .factura__header{
  display: none;
}

.factura.factura-es-solo-una-columna .factura__header div{
  display: none;
}

.factura.factura-es-solo-una-columna .factura__header div:first-child{
  display: block;
  width: 100%;
}

.factura.factura-es-solo-una-columna .factura__body .factura-fila .factura-columna{
  display: none;
}

.factura.factura-es-solo-una-columna .factura__body .factura-fila .factura-columna:first-child {
  display: block;
  width: 100%;
}

.factura-lista-principal,
.factura-lista-secundaria {
  word-wrap: break-word;
}

.factura-lista-principal span,
.factura-lista-secundaria span {
  float: right;
}

.factura-lista-principal {
  margin-bottom: 5px;
}
.factura-lista-principal span {
  color: #222;
  font-size: 15px;
}
.factura-lista-secundaria {
  color: #777;
}
.factura-lista-secundaria span {
  color: #999;
  font-size: 11px;
}

.factura__estado--borrador {
  color: #999 !important;
  text-transform: uppercase;
  font-size: 11px;
}

.factura__estado--enviado {
  color: #1093de !important;
  text-transform: uppercase;
  font-size: 11px;
}

.factura__estado--pagado {
  color: #388a10 !important;
  text-transform: uppercase;
  font-size: 11px;
}

.factura__estado--cancelado {
  color: #666 !important;
  text-transform: uppercase;
  font-size: 11px;
}



