
.file {
  display: inline-block;
}

.file__actions {
  visibility: hidden;
  text-align: center;
  padding: 2px 5px;
}

.file:hover .file__actions{
  visibility: visible;
}

.file__actions a {
  text-decoration: none;
  color: #222;
}



