.breadcrumb {
  width: 100%;
  background: #FFF;
  border-bottom: 1px solid #c2cfd6;
  padding: 12px 16px;
  list-style: none;
  margin-bottom: 0px;
}

.breadcrumb-item {
  color: #536c79;
  display: inline-block;
}

.breadcrumb-item a{
  text-decoration: none;
  color: #20a8d8;
}
.breadcrumb-item a:hover {
  color: #167494;
}

.breadcrumb-item + .breadcrumb-item::before{
  content: "/";
  color: #536c79;
  display: inline-block;
  padding: 0px 8px;
}



