.upload {
  position: relative;
  border: 1px dashed #ccc;
  padding: 5px;
  background: #FAFAFA;
  min-height: 100px;
  text-align: center;
}

.upload__placeholder {
  position: absolute;
  left: 0px;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  width: 160px;
  height: 80px;
  z-index: 1;
  color: #CCC;
  text-align: center;
}

.upload__files {
  position: relative;
  z-index: 3;
  text-align: left;
  display: inline-block;
}

.upload__files ul, .upload__files li {
  margin: 0;
  padding: 0;
  list-style: none;
  display: inline-block;
}

.upload__files li {
  padding: 5px;
  vertical-align: top;
}

.upload__files li img {
  height: 80px;
  border: 1px solid #CCC;
}



