.almacen__container
{
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 0;
  height: 160px;
  margin-left: 50px;
  margin-right: 10px;
  margin-bottom: 40px;
  position: relative;
}
.almacen__unity
{
  margin-left: 50px;
  margin-right: 10px;
  margin-bottom: 40px;
}


.almacen__estrella{
  width: 13px;
  bottom: 7px;
  position: absolute;
  right: 5px;
  z-index: 3;
  fill: #fff;
  color: #fff;
  font-size: 13px;
  
}

.almacen__triangulo{
  border-bottom: 44px solid #978BFD;
  border-left: 44px solid transparent;
  position: absolute;
  bottom: 0;
  right: 0;
  border-bottom-right-radius: 4px;
  border-left-color: #fff;
}

.almacen__nombre{
  width: 30%;
  float: left;
  height: 100%;
  background-color: #f0f5f7;
  border-radius: 4px 0 0 4px;
  padding: 68px 10px;
  text-align: center;
  font-weight: 500;
  font-size:15px;
}

.almacen__body{
  width: 70%;
  float: right;
  padding: 25px;
  height: 100%;
  
}

.almacen__body .almacen__botones
{
  position: absolute;
  top: 8%;
  right: 2%;
}


