.orden-pedido__total-section {
  padding: 20px 5px;
}
.total-section__fila {
  margin-bottom: 0px;
  margin-top: 0px;
  padding-top: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid #eee;
  padding-bottom: 10px;
  font-size: 15px;
}

.total-section__fila-final {
  margin-bottom: 0px;
  margin-top: 0px;
  padding-bottom: 10px;
  padding-top: 10px;
  background-color: #f7f7f7;
  color: #1685c6;
}

.total-section__fila .control-label,
.total-section__fila div {
  font-size: 15px;
  font-weight: 400;
}

.total-section__fila-final .control-label,
.total-section__fila-final div {
  font-size: 15px;
  font-weight: 400;
  padding-top: 0;
  padding-bottom: 0;
}

.orden-venta__resaltado {
  background: #FBFAFA;
  padding-top: 15px;
  padding-bottom: 15px;
  margin-bottom: 15px;
}
.orden-venta__formulario {
  padding: 0 15px 15px;
}



