header{
  position: fixed;
  width: 100%;
  height: 50px;
  z-index: 4;
}

.nav {
  display: -ms-flexbox;
  display: flex;
}
.navbar {
  margin-bottom: 0px;
  background: -webkit-linear-gradient(285deg, #978CFF 50%, #00C8FF );
  background: -o-linear-gradient(285deg, #978CFF 50%, #00C8FF );
  background: linear-gradient(165deg, #978CFF 50%, #00C8FF );
  border-radius: 0%;
  border: 0px;
}
.navbar-header {
  float: left;
  width: 200px;
  height: 50px;
  color: #ffffff;
  padding: 15px 15px;
}

.navbar-header img{
  height: 20px;
  display: block;
  margin: 0 auto;
}

.navbar-space{
  height: 32px;
}
.navbar-wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  height: 50px;
  color: #ffffff;
}
.navbar-wrapper ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: inline-block;
}
.navbar-wrapper ul li {
  margin: 0;
  display: inline-block;
}
.navbar-wrapper ul li a {
  color: #ffffff;
  display: inline-block;
  padding: 10px 15px;
  text-decoration: none;
}
.navbar-wrapper ul li a:hover {
  color: #00F0DC;
  opacity: 1;
}
.header-buscador {
  padding: 0 2px;
  border-radius: 0 20px 20px 0;
  border: 1px solid #FFF;
  border-left: 0;
  font-weight: 300;
  font-size: 15px;
  height: 28px;
  outline: none;
  -webkit-box-shadow: none;
          box-shadow: none;
  -webkit-transform: translateY(-2px);
      -ms-transform: translateY(-2px);
          transform: translateY(-2px);
  color: black;
}
.header-buscador::-webkit-input-placeholder {
  color: #777;
  font-size: 14px;
  font-weight: 300;
}
.header-buscador:-ms-input-placeholder {
  color: #777;
  font-size: 14px;
  font-weight: 300;
}
.header-buscador::-ms-input-placeholder {
  color: #777;
  font-size: 14px;
  font-weight: 300;
}
.header-buscador::placeholder {
  color: #777;
  font-size: 14px;
  font-weight: 300;
}
.header-buscador-icono {
  border-radius: 20px 0 0 20px;
  -webkit-transform: translateY(-3px);
      -ms-transform: translateY(-3px);
          transform: translateY(-3px);
  font-size: 14px;
  height: 28px;
  color: #978CFF !important;
  background: white;
  border: 1px solid #FFF;
  border-right: 0;
  padding: 2px 7px !important;
  margin-left: 12px;
}
.navbar-userimg
{
  border-radius: 50%;
  width: 30px;
  height: 30px;
}

.navbar-user-name {
  text-transform: uppercase;
  font-weight: 500;
  font-size: 12px;
  -webkit-font-smoothing: antialiased;
}

.header-icon
{
  font-size: 18px;
}

.header-icon-logo
{
  font-size: 17px;
}


.header-icon-plus
{
  font-size: 18px;
}

