@media only screen and (max-width: 1090px) {
  .navbar-user-name {
    display: none !important;
  }
}
@media only screen and (max-width: 900px) {
  .navbar-header {
    width: 140px;
  }
  .header-buscador {
    width: 120px;
  }
}


