/*Inicio Acordion Styles*/
.accordion {
  list-style: none;
  margin: 0;
  padding: 0;
}

.accordion input[type=checkbox] {
  visibility: hidden;
}

.accordion-panel {
  display: block;
  margin: 0;
  padding: 0;
  position: relative;
}

.accordion-panel:after {
  clear: both;
  content: '';
  display: table;
}

.accordion-panel--between,
.accordion-panel--last {
}

.accordion-title {
  background: #F9F9F9;
  color: #363636;
  cursor: pointer;
  display: block;
  font-weight: 100;
  padding: 0px;
  padding-bottom: 7px;
  padding-top: 0px;
  position: relative;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  border-bottom: 1px solid #eee;
  letter-spacing: 0.02rem;
  font-size: 15px;

}

.accordion-title--checked {
  -webkit-transition: background-color 125ms ease-in-out, border-radius 62.5ms ease-in-out;
  -o-transition: background-color 125ms ease-in-out, border-radius 62.5ms ease-in-out;
  transition: background-color 125ms ease-in-out, border-radius 62.5ms ease-in-out;
}

.accordion-title--unchecked {
  -webkit-transition: background-color 125ms ease-in-out, border-radius 125ms ease-in-out 312.5ms;
  -o-transition: background-color 125ms ease-in-out, border-radius 125ms ease-in-out 312.5ms;
  transition: background-color 125ms ease-in-out, border-radius 125ms ease-in-out 312.5ms;
}

/* fix iOS touch not triggering a click from child elements */
.accordion-title > * {
  pointer-events: none;
}

.accordion-title--first {
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
}

.accordion-animator--last,
.accordion-content--last,
.accordion-title--last.accordion-title--no-content,
.accordion-title--last.accordion-title--unchecked {
  border-bottom-left-radius: 3px;
  border-bottom-right-radius: 3px;
}

.accordion-title--content:before {
  content: "\f107";
  font: normal normal normal 14px/1 FontAwesome;
  font-size: 1.5em;
  line-height: 0rem;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: absolute;
  right: 0rem;
  top: 1rem;
  -webkit-transform: rotate(0deg);
  -ms-transform: rotate(0deg);
      transform: rotate(0deg);
  -webkit-transition: -webkit-transform 375ms ease-in-out;
  transition: -webkit-transform 375ms ease-in-out;
  -o-transition: transform 375ms ease-in-out;
  transition: transform 375ms ease-in-out;
  transition: transform 375ms ease-in-out, -webkit-transform 375ms ease-in-out;
}

.accordion-title--checked {
  background: #F9F9F9;
}

.accordion-title--checked:before {
  -webkit-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
      transform: rotate(180deg);
}

.accordion-title:active:before,
[data-state="tabbordion"]:focus ~ .accordion-title:before {
  color: #7768FD;
}

.accordion-animator {
  background-color: #F9F9F9;
  -webkit-transition: background-color 125ms ease-in-out, height 375ms ease-in-out;
  -o-transition: background-color 125ms ease-in-out, height 375ms ease-in-out;
  transition: background-color 125ms ease-in-out, height 375ms ease-in-out;
}

.accordion-animator:after {
  clear: both;
  content: '';
  display: block;
}

.accordion-animator--checked {
  background-color: #F9F9F9;
}

.accordion-content {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  color: #363636;
  display: none;
  float: left;
  padding: 0 1em;
  width: 100%;
}

.accordion-content--animated {
  display: block;
  opacity: 0;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  -webkit-transition: opacity 375ms ease-in-out, transform 375ms ease-in-out;
  -webkit-transition: opacity 375ms ease-in-out, -webkit-transform 375ms ease-in-out;
  transition: opacity 375ms ease-in-out, -webkit-transform 375ms ease-in-out;
  -o-transition: opacity 375ms ease-in-out, transform 375ms ease-in-out;
  transition: opacity 375ms ease-in-out, transform 375ms ease-in-out;
  transition: opacity 375ms ease-in-out, transform 375ms ease-in-out, -webkit-transform 375ms ease-in-out;
}

.accordion-content--marginTop {
  margin-top: -65535px;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  -webkit-transition: margin-top 375ms ease-in-out, opacity 375ms ease-in-out, transform 375ms ease-in-out;
  -webkit-transition: margin-top 375ms ease-in-out, opacity 375ms ease-in-out, -webkit-transform 375ms ease-in-out;
  transition: margin-top 375ms ease-in-out, opacity 375ms ease-in-out, -webkit-transform 375ms ease-in-out;
  -o-transition: margin-top 375ms ease-in-out, opacity 375ms ease-in-out, transform 375ms ease-in-out;
  transition: margin-top 375ms ease-in-out, opacity 375ms ease-in-out, transform 375ms ease-in-out;
  transition: margin-top 375ms ease-in-out, opacity 375ms ease-in-out, transform 375ms ease-in-out, -webkit-transform 375ms ease-in-out;
}

.accordion-content--checked {
  display: block;
  opacity: 1;
}

.accordion-content--checked.accordion-content--animated {
  -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
          transform: translateY(0);
}

.accordion-content--checked.accordion-content--marginTop {
  margin-top: 0;
}

/*Fin Acordion Styles*/

.cuenta-detalle-body--tab {
  height: calc(100vh - 200px) !important;
}


