.prevista {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  height: calc(100vh - 50px);
}
.prevista-principal {
  display: inline-block;
  width: 100%;
  position: relative;
}
.prevista-detalle {
  border-left: 1px solid #c2cfd6;
  width: 0%;
  display: none;
  overflow: hidden;
}

.prevista-detalle--visible {
  display: inline-block;
}
.prevista-principal.prevista--abierta {
  width: 30%;
}
.prevista-detalle.prevista--abierta {
  width: 70%;
  display: inline-block;
}



