.paginacion {
  width: 100%;
  padding: 10px 10px 30px;
}

.paginacion__total,
.paginacion__por-pagina,
.paginacion__paginas {
  display: inline-block;
  padding-left: 5px;
  padding-right: 5px;
}

.paginacion__por-pagina,
.paginacion__paginas {
  float: right;
}

.paginacion__total {
  padding-top: 5px;
  padding-bottom: 4px;
}
.paginacion__por-pagina {
  width: 100px;
}
.paginacion__paginas {
  padding-top: 3px;
  padding-bottom: 4px;
}
.paginacion__paginas button {
  margin-left: 5px;
  margin-right: 5px;
}



