.cotizacion__resaltado {
  background: #FBFAFA;
  padding-top: 15px;
  padding-bottom: 15px;
  margin-bottom: 15px;
}
.cotizacion__formulario {
  padding: 0 15px 15px;
}

.address-cotizacion-formulario__header {
  color: #777;
  text-transform: uppercase;
  font-weight: 400;
}

.address-cotizacion-formulario__header a {
  text-decoration: none;
  font-size: 10px;
  font-weight: 500;
}

.address-cotizacion-formulario__body,
.address-cotizacion-formulario__body b,
.address-cotizacion-formulario__body div {
  font-size: 12px;
  line-height: 18px;
  color: #777;
}

.address-cotizacion-formulario__body a {
  text-decoration: none;
  font-size: 12px;
}



