.table-pedidos {
  width: 98%;
}
.table-pedidos thead {
  color: #666;
}
.table-pedidos thead th {
  border: 1px solid #dadada;
  background: #f9f9f9;
  padding: 8px 10px;
  vertical-align: top;
  font-size: 12px;
  text-transform: uppercase;
  font-weight: 400;
}

.table-pedidos tbody td {
  border: 1px solid #dadada;
  position: relative;
  vertical-align: top;
}

.table-pedidos tbody tr td:first-child {
  vertical-align: middle;
}
.table-pedidos tbody tr td:last-child {
  padding: 5px;
}

.table-pedidos .form-control {
  border: none !important;
  border-radius: 0 !important;
  resize: none;
}

.table-pedidos .select-searched {
  border: none !important;
  border-radius: 0 !important;
}

.table-pedidos tbody tr td:nth-child(2) .select-searched{
  background: #f9f9f9;
  font-weight: 400;
}



