.select-buscador {
  position: absolute;
  top: 45px;
  left: 350px;
  z-index: 995;
  padding: 5px 0;
  margin: 2px 0 0;
  background-color: #fff;
  border: 1px solid rgba(0,0,0,.15);
  -webkit-box-shadow: 0 6px 12px rgba(0,0,0,.175);
          box-shadow: 0 6px 12px rgba(0,0,0,.175);
  list-style: none;
  width: 267px;
  display: none;
}
.select-buscador--abierto {
  display: block;
}
.select-buscador > li > a {
  padding: 3px 20px;
  padding-left: 33px;
  clear: both;
  font-weight: 400;
  color: #333;
  display: block;
  position: relative;
}
.select-buscador > li > a:hover {
  text-decoration: none;
  background-color: #2e8fda;
  color: white;
}
.select-buscador > li > a > .select-buscador-icono {
  position: absolute;
  top: 6px;
  left: 10px;
}


