.select-modal__table {
  width: 100%;
  margin: 10px 0px;
}
.select-modal__table thead tr th {
  font-size: 14px;
  background-color: #F7F9FB;
  padding: 8px;
  font-weight: 400;
  text-align: center;
  border: 1px solid #efefef;
}

.select-modal__table tbody tr td {
  border: 1px solid #efefef;
  padding-left: 5px;
  padding-right: 5px;
}

.select-modal__table tbody tr.active {
  background: #f9f9f9;
}



