
.tarifas-producto__table {
  overflow-x: scroll;
  background-color: #FFF;
  position: relative;
  max-height: 400px;
}

.tarifas-producto__body{
  margin-bottom: 20px;
  border: 1px solid #e6e6e6;
}

.tarifas-producto__header {
  font-weight: 600;
  vertical-align: middle;
  width: 100%;
  background: #ECEFF1;
  border-top: 1px solid #e6e6e6;
}

.tarifas-producto__header div {
  color: #263035;
  display: inline-block;
  width: 33%;
  padding: 10px 8px 7px 20px;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 12px;
  vertical-align: bottom;
  -webkit-font-smoothing: antialiased;
}

.tarifas-producto__header .porcentajeveinte{
  width: 25%;
  text-align: right;
}

.tarifas-producto__fila .porcentajeveinte{
  width: 25%;
}
.tarifas-producto__fila .porcentajetreinta{
  position: relative;
  width: 46%;
  vertical-align: top;
}

.tarifas-producto__fila {
  width: 100%;
  border-top: 1px solid #e9ecef;
  -webkit-transition: background-color 0.2s linear;
  -o-transition: background-color 0.2s linear;
  transition: background-color 0.2s linear;
  cursor: pointer;
  word-wrap: break-word;
}
.tarifas-producto__txt-info
{
  color: #388a10;
  font-style: italic;
}

.tarifas-producto__fila.active,
.tarifas-producto__fila:hover {
  background-color: #F5F5F5;
}

.tarifas-producto__fila div {
  display: inline-block;
  width: 33%;
  padding: 12px 12px 12px 20px;
  vertical-align: top;
}

.tarifa-producto__text
{
  width: 83%;
  padding: 0px 0px 0px 35px!important;
}

.tarifa-producto__form-group
{
  width: 83%!important;
  padding: 5px 0px 0px 0px!important;
  margin-bottom: 0px!important;
}
.tarifa-producto__estandar
{
  text-align: right;
}


