.historia-header {
  width: 240px;
  background: #FFF;
  position: absolute;
  top: 45px;
  left: 257px;
  z-index: 994;
  border: 1px solid rgba(0,0,0,.15);
  -webkit-box-shadow: 0 6px 12px rgba(0,0,0,.175);
          box-shadow: 0 6px 12px rgba(0,0,0,.175);
  border-radius: 2px;
  list-style: none;
  display: none;
}
.historia-header--abierto {
  display: block;
}
.historia-header__link {
  color: #666;
  display: block;
  padding: 3px 20px;
  font-weight: 400;
}
.historia-header__link:hover {
  background-color: #2e8fda;
  text-decoration: none;
  color: white;
}
.historia-header__tag {
  font-size: 8px;
  color: #d4d4d4;
  margin-left: 29px;
}
.historia-header__icono {
  font-size: 12px;
  margin-right: 10.5px;
}


