.page_login {º
  background-position: no-repeat;
  background-size: cover;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

.page_login__body {
  padding-top: 200px;
}

.page_form {
  -webkit-box-shadow: 0 1px 4px 0 rgba(0,0,0,.14);
  box-shadow: 0 1px 4px 0 rgba(0,0,0,.14);
  border-radius: 6px;

  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  background: white;
  padding-left: 25px;
  padding-right: 25px;
  padding-top: 15px;
  padding-bottom: 25px;
}

.page_form .input-group-addon {
  background: white !important;
}

.page_form__header {
  margin-bottom: 30px;
  margin-top: -40px;
  padding: 15px;
  background: #7768FD;
  color: white;
  background: -webkit-linear-gradient(30deg,#978BFD,#7768FD);
  background: -o-linear-gradient(30deg,#978BFD,#7768FD);
  background: linear-gradient(60deg,#978BFD,#7768FD);
  -webkit-box-shadow: 0 4px 20px 0 rgba(0,0,0,.14), 0 7px 10px -5px rgba(30, 37, 233, 0.4);
  box-shadow: 0 4px 20px 0 rgba(0,0,0,.14), 0 7px 10px -5px rgba(30, 37, 233, 0.4);
  text-align: center;
  border-radius: 3px;
}

.page_form__btn-login button{
  width: 100%;
}



