.tabla--simple {
  width: 100%;
  max-width: 100%;
  font-size: 13px;
  background: white;
}

.tabla--simple thead tr th{
  vertical-align: middle;
  border-bottom: 1px solid #ebebeb;
  font-weight: 600;
  padding: 12px 20px;
}
.tabla--simple tbody tr td{
  vertical-align: middle;
  border-bottom: 1px solid #ebebeb;
  padding: 12px 12px 12px 20px;
  position: relative;
}

.tabla--header, .tabla--body {
  display: table-row-group;
}

.tabla--simple tbody tr td:first-child {
  padding-left: 35px;
}

.table-links {
  text-decoration: none;
  display: inline-block;
  padding: 5px;
  color: #4d5259;
}

.table-links--editar:hover {
  color: #33cabb;
}

.table-links--eliminar:hover {
  color: #f96868;
}

.table-links--indicador-hijos {
  position: absolute;
  left: 20px;
  top: 8px;
}

.categoria {
  min-width: 768px;
  overflow-x: scroll;
  background-color: #FFF;
  position: relative;
}

.categoria-header {
  vertical-align: middle;
  width: 100%;
  text-transform: uppercase;
  font-weight: 600;
  -webkit-font-smoothing: antialiased;
  font-size: 1.09rem;
  background: #f5f5f5;
}

.categoria-header div {
  color: #263035;
  display: inline-block;
  width: 20%;
  padding: 10px 8px 7px 20px;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 12px;
  vertical-align: bottom;
  -webkit-font-smoothing: antialiased;
}

.categoria-header div:first-child {
  padding-left: 35px;
  width: 30%;
}
.categoria-header div:last-child {
  width: 10%;
}


.categoria-fila {
  width: 100%;
  border-top: 1px solid #e9ecef;
  -webkit-transition: background-color 0.2s linear;
  -o-transition: background-color 0.2s linear;
  transition: background-color 0.2s linear;
}
.categoria-fila:hover {
  background-color: #f9fafb;
}

.categoria-fila div {
  display: inline-block;
  width: 20%;
  padding: 12px 12px 12px 20px;
}

.categoria-fila div:first-child {
  display: inline-block;
  width: 30%;
  position: relative;
  padding-left: 35px;
}
.categoria-fila div:last-child {
  width: 10%;
}

.categoria-fila-estado{
  background-color: #51cf66;
  text-transform: uppercase;
  font-size: 1.09rem;
  border-radius: 4px;
  font-weight: 700;
  padding: .5rem;
  color: #fff;
}

.categoria-fila-estado-desactivo{
  background-color: #CF001B;
  text-transform: uppercase;
  font-size: 1.09rem;
  border-radius: 4px;
  font-weight: 700;
  padding: .5rem;
  color: #fff;
}

.categoria-hijos-isopen {

}



