.config-usuarios__table {
  width: 100%;
}

.config-usuarios__imagen-default {
  display: inline-block;
  font-size: 50px;
  padding: 5px 25px;
}
.config-usuarios__detalle {
  display: inline-block;
}

.config-usuarios__table thead {
  font-size: 12px;
  text-transform: uppercase;
}
.config-usuarios__table thead > tr > th {
  border-top: 1px solid #e6e6e6;
  border-bottom: 1px solid #e6e6e6;
  color: #263035;
  background-color: #f7f7f7;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  padding: 8px;
}

.config-usuarios__table tbody > tr > td {
  padding: 8px;
  line-height: 20px;
  vertical-align: middle;
  border-top: 1px solid #ededed;
  color: #222;
  border-bottom: 1px solid #ededed;
}

.config-usuarios__acciones {
  overflow: hidden;
  padding: 10px;
}

.config-usuarios__link {
  text-decoration: none !important;
  font-weight: 300;
}



